import React from 'react';
import styled, { css } from 'styled-components';
import theme from '../theme';

export const StyledTypographyComponent = styled(
  ({ type, variant, children, ...props }) =>
    React.createElement(type || 'div', props, children)
)`
  font-family: 'Rubik', sans-serif;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: ${theme.textLinkColors};

  ${({ type, variant }) =>
    type &&
    css`
      color: ${variant ? theme?.[type] : ''};

      ${type &&
      css`
        font-size: ${type === 'h1'
          ? '48px'
          : type === 'h2'
          ? '36px'
          : type === 'h3'
          ? '28px'
          : type === 'h4'
          ? '18px'
          : type === 'h5'
          ? '14px'
          : type === 'h6'
          ? '10px'
          : 'inherit'};
        font-weight: ${type === 'h1'
          ? '800'
          : type === 'h2'
          ? '700'
          : type === 'h3'
          ? '600'
          : type === 'h4' || type === 'h5'
          ? '500'
          : type === 'h6'
          ? '400'
          : 'inherit'};
        height: ${type === 'h1'
          ? '50.3984px'
          : type === 'h2'
          ? '45px'
          : type === 'h3'
          ? '35px'
          : type === 'h4'
          ? '21.9531px'
          : type === 'h5'
          ? '17.0781px'
          : type === 'h6'
          ? '12.1953px'
          : 'inherit'};
        line-height: ${type === 'h1' ? '50.4px' : 'inherit'};
      `}
    `}
`;

export const Divider = styled.hr`
  border: 0.5px inset ${theme.textLinkColors};
  display: block;
  width: 1000px;
`;
